import React, { Component } from "react";
import { Button, Modal, Input } from 'antd';
import Apis from '@/apis'
import { AdvanceTable, } from '@/components';
const { Search } = Input

class View extends Component {
  state = {
    data: [],
    keyword: '',
    selectedRowKeys: [],
    visible: false
  }

  componentDidMount(){
    const { value } = this.props
    if(value) this.setState({ selectedRowKeys: value })
    this.fetchData()
  }
  
  fetchData(data={}){
    const { api, params={}, } = this.props
    if(Apis[api]){
      Apis[api]({
        ...params,
        ...data
      }).then(res => {
        if(Array.isArray(res.data.list)){
          this.setState({
            data: res.data.list
          })
        }else{
          this.setState({
            data: []
          })
        }
      })
    }
  }
  handleSearch = e => {
    e.preventDefault && e.preventDefault();
    let { keyword } = this.state;
    const { searchFields } = this.props
    if(this.adTable){
      let data = {$or: []}
      for(let item of searchFields){
        let obj = {}
        obj[item.value] = { $regex: keyword }
        data['$or'].push(obj)
      }
      this.adTable.fetch(data)
    }
  };

  changeKeyword = e => {
    this.setState({
      keyword: e.currentTarget.value
    });
  };
  onSelectChange = selectedRowKeys => {
    console.log('selectedRowKeys changed: ', selectedRowKeys);
    this.setState({ selectedRowKeys });
  };

  onChange = () => {
    this.setState({ visible: false });
    let { data, selectedRowKeys } = this.state
    let dataMap = {}
    data.forEach(item => {
      dataMap[item._id] = item
    })
    const { onChange } = this.props
    if(onChange) onChange(selectedRowKeys.map(val => dataMap[val]))
  };

  render() {
    const { api, columns=[], width=800, valuepropname, btnText='添加', title='标题', searchFields=[] } = this.props
    const { visible, selectedRowKeys } = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    return (
      <>
        <Button type="primary" onClick={e => this.setState({visible: true})}>
          {btnText}
        </Button>
        <Modal
          title={title}
          visible={visible}
          onOk={this.onChange}
          onCancel={e => this.setState({visible: false})}
          okText="确认"
          cancelText="取消"
          width={width}
          bodyStyle={{padding: 0}}
        >
          <Search
            placeholder={`请输入${searchFields.map(item => item.label).join('/')}关键字`}
            onSearch={this.handleSearch}
            onChange={this.changeKeyword}
            style={{width: '50%', margin: 8}}
            enterButton
          />
          <AdvanceTable
            ref={table => this.adTable = table}
            columns={columns}
            pageSize={20}
            size={'small'}
            rowSelection={rowSelection}
            parseParams={({pageSize, current, filters, sorter}) => {
              return {
                limit: pageSize,
                page: current,
              }
            }}
            rowKey={record => record[valuepropname || '_id']}
            showSetting={false}
            bordered={false}
            api={api}
          />
        </Modal>
      </>
    );
  }
}
export default View